<template>

  <div class="admin_panel">
    <h1>ADMIN PANEL</h1>
    <h3>mesto gde mozes brisati korisnike</h3>
    <hr>
    <div style="height: 4rem"></div>
    <!--    <div style="height: 4rem">{{proizvodi}}</div>-->

    <div class="tabela_korisnika">
      <b-table
          sticky-header="100%"
          :items="resi(users)"
          :fields="fields"
          head-variant="light"
      >
        <template v-slot:cell(action)="row">
          <b-button variant="danger" @click="izbrisi(row.item.id)">izbrisi</b-button>
        </template>
      </b-table>
    </div>


  </div>
</template>

<script>
// @ is an alias to /src

import { mapState, mapActions } from 'vuex';


export default {
  name: "Admin_panel",
  computed: {
    ...mapState(['users'])
  },
  data() {
    return {

      fields: [
        { key: 'action'},
        { key: 'id' },
        { key: 'username' },
        { key: 'is_admin' },

        // { z: localStorage.getItem('user_id') },
  ]
    }
  },

  methods: {
    ...mapActions(['load_users','delete_user']),

    resi: function (proizvod){
      return proizvod;
    },
    izbrisi: function (id){
      this.delete_user(id);
      this.load_users()
    }

  },

  mounted: function() {

    this.load_users();

  }
}

</script>
